@mixin displayFlexCenter() {
    display: flex;
    justify-content: center;
    align-items: center;
  }

@mixin absolouteCenter(){
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
}

@mixin hSpaceBetweenVcenter {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
